@import url('~uikit/dist/css/uikit.css');
@import url('~sweetalert2/dist/sweetalert2.css');
@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700');

* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: 'Source Sans Pro', sans-serif;
}

html {
   background: #02576c;
}

.header {
   background: #05a19c !important;
}

.title {
   margin-bottom: 0;
   text-transform: uppercase;
   font-weight: 700;
   color: white;
}

.subtitle {
   text-transform: uppercase;
   font-weight: 500;
}

.card {
   margin: 10px auto;
   padding: 30px 15px;
   border-radius: 7px;
}

.sub-card {
   margin: 30px 20px;;
}

.search-btn {
   margin-top: 24px;
}

.form {
   justify-content: center;
}

.image {
   width: 100%;
   height: 100%;
   vertical-align: middle;
}